import "./style.scss";

class RateButton {
  constructor({ classId }) {
    this.classId = classId;

    this.itemClass = "js-rate-button__item";
    this.itemSelectedClass = "rate-button__item_selected";

    this.rate = 0;
  }

  start() {
    this._initDom();
    this._initEvents();
  }

  getRate() {
    return this.rate;
  }

  _initDom() {
    this.itemsEl = [...document.querySelectorAll(`.${this.classId} .${this.itemClass}`)];
  }

  _initEvents() {
    this.itemsEl.forEach((itemEl) => {
      itemEl.addEventListener("click", this._clickItemEvent.bind(this));
    });
  }

  _selectRate(currentItemEl) {
    // if item is the same as selected - toggle class
    if (currentItemEl.classList.contains(this.itemSelectedClass)) {
      this.rate = 0;
      currentItemEl.classList.remove(this.itemSelectedClass);
      return;
    }

    this.itemEl.forEach((itemEl) => {
      itemEl.classList.remove(this.itemSelectedClass);
    });

    this.rate = parseInt(currentItemEl.getAttribute("data"), 10);
    currentItemEl.classList.add(this.itemSelectedClass);
  }

  _clickItemEvent(e) {
    e.preventDefault();
    e.stopPropagation();

    const currentItemEl = e.currentTarget;

    this._selectRate(currentItemEl);
  }
}

export default RateButton;
